
import { defineComponent } from 'vue'
import Tokenizer from '@/tokenizer'
import { testapikey } from '@/docs/helpers/testkeys'

export default defineComponent({
  name: 'Home',
  data () {
    return {
      example: (null as any),
      exampleResponse: (null as null | any),
      validCard: (null as any)
    }
  },
  mounted () {
    // Testing local storage for guardian stuff
    var data = {
      events: [
        {
          data: {
            type: 'sift_action_happened',
            action: 'hello world'
          }
        }
      ]
    } as any
    localStorage.setItem('fp-guardian-results', JSON.stringify({value: data}))

    this.example = new Tokenizer({
      apikey: testapikey,
      container: this.$refs.example as HTMLDivElement,
      // settings: {
      //   payment: {
      //     calculateFees: false
      //   }
      // },
      submission: (resp: any) => {
        this.exampleResponse = resp
      },
      validCard: (card: any) => {
        this.validCard = card
      }
    })
  }
})
